import React, { PureComponent } from "react";
import { GET_MEDIA_NAME } from "../../../utils";
import { styleHeaderBanner } from "./Elements/HeaderConstant";

export default class HeaderType3 extends PureComponent {
  render() {
    const { Banner } = this.props;
    const { bannerStyle, subTitleStyle, titleStyle, iconAreaStyle, iconStyle, isFacebook, isTwitter, isInstagram, facebookIconAreaStyle, facebookIconStyle, twitterIconAreaStyle, twitterIconStyle, instagramIconAreaStyle, instagramIconStyle, subTitleVarentClass, titleVarentClass } = styleHeaderBanner(Banner);
    var bannerLogoImage = Banner.LogoImage;
    if (bannerLogoImage && String(bannerLogoImage).length > 0 && String(bannerLogoImage).includes("banner/logo_bnr") && String(bannerLogoImage).includes("https://cloud.taggbox.com/uploaded/")) {
      bannerLogoImage = String(Banner.LogoImage).replace("https://cloud.taggbox.com/uploaded/", "")
    } else if (bannerLogoImage.includes("https://cloud.taggbox.com/uploaded/") && bannerLogoImage.split('https://cloud.taggbox.com/uploaded/')[1] === '') {
      bannerLogoImage = String(Banner.LogoImage).replace("https://cloud.taggbox.com/uploaded/", "")
    }else if (bannerLogoImage && String(bannerLogoImage).length > 0){
      let LogoImage = bannerLogoImage.split('/').pop();
        if(LogoImage=='logo_bnr-2.svg' || LogoImage=='logo_bnr-3.svg' || LogoImage=='logo_bnr-5.svg'){
            bannerLogoImage = `https://cloud.taggbox.com/react-app/media/banner/logo_bnr-3.svg`
        }
   }

    var bannerCustomStyle = bannerStyle;
    if (bannerCustomStyle && bannerCustomStyle.backgroundImage.includes("bg-bnr-3.png") && bannerCustomStyle.backgroundImage.includes("https://cloud.taggbox.com/uploaded/")) {
      bannerCustomStyle.backgroundImage = bannerCustomStyle.backgroundImage.replace("https://cloud.taggbox.com/uploaded/", "https://test.taggbox.com/widget/assets/media/banner/")
    }

    return (
      <div className="tb_wall_header_wrap__" style={bannerCustomStyle}>
        <div className="tb_header3_row__">
          <div className="tb_header3_column_logo__">
            {bannerLogoImage ?
              <div className="tb_header3_logo__">
                <img loading="lazy" className="tb_header3_logo_img__" src={bannerLogoImage} alt={GET_MEDIA_NAME(bannerLogoImage)} width={180} height={52} />
              </div>
              : ''}
          </div>
          <div className="tb_header3_column_content__">
            <div className="tb_header3_content__">
              <div className={`tb_header3_subtitle__ tb-cTBfont-${subTitleVarentClass}`} style={subTitleStyle}>{Banner.subTitle}</div>
              <div className={`tb_header3_title__ tb-cTBfont-${titleVarentClass}`} style={titleStyle}>{Banner.title}</div>
            </div>
          </div>
          <div className="tb_header3_column_icons__">

            {Banner.social_icon_status === 1 ? <div className="tb_header3_social_icons__">
              {isFacebook ?
                <a href={Banner.facebook_url} target="_blank" className="tb_header3_social_icon_list__" style={{ ...iconAreaStyle, ...facebookIconAreaStyle }} rel="noopener noreferrer">
                  <div className={`tb_header3_social_icon__ tb__icon tb-facebook`} style={{ ...iconStyle, ...facebookIconStyle }}> </div>
                </a> : null}
              {isTwitter ?
                <a href={Banner.twitter_url} target="_blank" className="tb_header3_social_icon_list__" style={{ ...iconAreaStyle, ...twitterIconAreaStyle }} rel="noopener noreferrer">
                  <div className="tb_header3_social_icon__ tb__icon tb-twitter" style={{ ...iconStyle, ...twitterIconStyle }}></div>
                </a> : ''}
              {isInstagram ?
                <a href={Banner.instagram_url} target="_blank" className="tb_header3_social_icon_list__" style={{ ...iconAreaStyle, ...instagramIconAreaStyle }} rel="noopener noreferrer">
                  <div className="tb_header3_social_icon__ tb__icon tb-instagram" style={{ ...iconStyle, ...instagramIconStyle }}> </div>
                </a> : ''}
            </div> : null
            }</div>
        </div>
      </div>
    );
  }
}
